<template>
	<div class="training">
		<h1> </h1>
		<hr>
		<div class="progress">
		  <div class="progress-bar" :style="progressStyles"></div>
		</div>
		<div class="box">
			<transition name="flip" mode="out-in">
				<app-start-screen 
					v-if="state == 'start'"
					@onStart="onStart"
				>
				</app-start-screen>
				<app-question 
					v-else-if="state == 'question'"
					@success="onQuestSuccess"
					@error="onQuestError"
				>
				</app-question>
				<app-message 
					v-else-if="state == 'message'"
					:type="message.type"
					:text="message.text"
					@onNext="onNext"
				>
				</app-message>
				<app-result-screen v-else-if="state == 'result'"
									:stats="stats"
								   @repeat="onStart"
								   @nextLevel="nextLevel"
				>
				</app-result-screen>
				<app-next-level v-else-if="state == 'nextLevel'"
								@startNextLevel="nextLevelStart"
				>
				</app-next-level>	  
				 <app-next-level-start-screen v-else-if="state == 'nextLevelStart'"
												@repeat="onStart"
				 >
				 </app-next-level-start-screen>
				<div v-else>Unknown state</div>
			</transition>
		</div>
	</div>
</template>

<script>
export default {
	data () {
		return {
			state: 'start',
			stats: {
				success: 0,
				error: 0
			},
			message: {
				type: '',
				text: ''
			},
			qestMax: 3,
		}
	},
	computed: {
		questDone() {
			return this.stats.success + this.stats.error;
		},
		progressStyles() {
			return {
				width: (this.questDone / this.qestMax * 100) + '%'
			}
		}
	},
	methods: {
		onStart() {	
			this.stats.success = 0;
			this.stats.error = 0;	
			this.state = 'question';
		},
		onQuestSuccess() {
			this.state = 'message';
			this.message.text = ',  !';
			this.message.type = 'success';
			this.stats.success++;
		},
		onQuestError(msg) {
			this.state = 'message';
			this.message.text = ', - ' + msg;
			this.message.type = 'warning';
			this.stats.error++;
		},
		onNext () {
			if (this.questDone < this.qestMax) {
				this.state = 'question';
			} else {
				this.state = 'result';
			}
			
		},
		nextLevel() {
			this.state = 'nextLevel';
		},
		nextLevelStart() {
			this.state = 'nextLevelStart';
		}
	},
}
</script>

<style scoped>
	.training {
		max-width: 700px;
		margin: 20px auto;
	}
	
	.progress-bar {
		transition: 0.3s;
	}

	.box {
		margin: 10px 0;
	}

	.flip-enter {

	}

	.flip-enter-active {
		animation: flipInX 0.1s linear;
	}

	.flip-leave {

	}

	.flip-leave-active {
		animation: flipOutX 0.1s linear;
	}

	@keyframes flipInX {
		from { transform: rotateX(90deg); }
		to { transform: rotateX(0deg); }
	}

	@keyframes flipOutX {
		from { transform: rotateX(0deg); }
		to { transform: rotateX(90deg); }
	}

</style>



