<template>
	<div class="alert alert-primary">
		<h3>  : {{ stats.success }}</h3>
		<h3>: {{ stats.error }}</h3>
		<hr>
		<button class="btn btn-primary" @click="$emit('repeat')"> </button>
		<button class="btn btn-primary" @click="$emit('nextLevel')"> </button>
	</div>
</template>

<script>
	export default {
		props: ['stats']
	}
</script>

<style>
	
</style>


